.main {
  height: 100%;
  width: 100%;
  background-color: rgb(21, 21, 21);
}

.Auth-form-container {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  margin-top: 10px;
  margin-right: 5%;
}

.Auth-form {
  min-width: 28em;
  /* height: 35em; */
  box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px;
  position: relative;
  padding-top: 30px;
  padding-bottom: 20px;
  border-radius: 8px;
  background-color: white;
}

.Auth-form-content {
  padding-left: 12%;
  padding-right: 12%;
}

.Auth-form-title {
  text-align: center;
  margin-bottom: 1em;
  font-size: 24px;
  color: rgb(34, 34, 34);
  font-weight: 800;
}

label {
  font-size: 14px;
  font-weight: 600;
  color: rgb(34, 34, 34);
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.funnelChart {
  -webkit-clip-path: polygon(0 0, 100% 0, 60% 78%, 60% 90%, 40% 100%, 40% 78%);
  clip-path: polygon(0 0, 100% 0, 60% 78%, 60% 90%, 40% 100%, 40% 78%);
  text-align: center;
}

.funnelChart .chartSegment {
  width: 100%;
  min-height: 1px;
  font-size: 12px;
  padding: 10px 0;
  margin: 2px 0;
  color: black;
  background-color: yellow;
}

.funnelChart .chartSegment:last-child {
  padding-bottom: 40px;
}

.funnelChart .chartSegment:hover {
  opacity: 0.7;
}

.funnelChart .chartSegment .segmentTitle {
  font-size: 1.1rem;
  padding-bottom: 5px;
}
